import {Injectable} from '@angular/core';

import {UploaderMode, UploaderSelectionMode} from '../../common/uploader/enums/uploader.enum';
import {FileSize} from '../../common/uploader/consts/uploader.consts';

@Injectable({
  providedIn: 'root'
})
export class AdaptRxUploaderConfigService {
  uploadMode: UploaderMode = UploaderMode.Manual;
  selectionMode: UploaderSelectionMode = UploaderSelectionMode.File;
  enableDnD: boolean = true;
  enableFileDialog: boolean = true;
  allowedTypes: string[] = [];
  forbiddenTypes: string[] = [];
  suppressParallel: boolean = false;
  filesCount: number = 1;
  allowDuplicates: boolean = false;
  showUploadFolderAlert: boolean = true;
  visibleFiles: number = 3;
  reusable: boolean = false;
  allowDeletion: boolean = true;
  customErrors: boolean = false;
  maxFileSize: string = (FileSize.MB * 20).toString();
  minFileSize: string = '0';
  chunkSize: string = FileSize.MB.toString();
  indeterminateFileLoader: boolean = false;

  url: string;
  deleteUrl: string;
}
